import MyBarChart from '@/components/MyBarChart'

function Home() {
  const option1 = {
    title: {
      text: '三大框架满意度'
    },
    xAxis: {
      type: 'category',
      data: ['Vue', 'React', 'Angular']
    },
    yAxis: {
      type: 'value'
    },
    series: [
      {
        data: [10, 40, 70],
        type: 'bar'
      }
    ]
  }
  const option2 = {
    title: {
      text: '三大框架使用度'
    },
    xAxis: {
      type: 'category',
      data: ['Vue', 'React', 'Angular']
    },
    yAxis: {
      type: 'value'
    },
    series: [
      {
        data: [10, 40, 70],
        type: 'bar'
      }
    ]
  }
  return (
    <>
      <MyBarChart options={option1} />
      <MyBarChart options={option2} />
    </>
  )
}

export default Home
